<template>
  <main id='app'>
    <canvas class='canvas' ref='canvas'></canvas>

    <transition name='fade'>
      <router-view v-if='imagesReady && loaded' v-show='isLoaded' />
      <div class="app-loading" v-else>
        <img class="app-loading-gif" src="/img/icons/pikachurun.gif" />
        <p class="app-loading-text">Game is loading...</p>
      </div>
    </transition>
  </main>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      imagesReady: false,
      loaded: false,
    };
  },
  computed: {
    isLoaded() {
      return this.$store.state.midori.isLoaded;
    },
    update() {
      return this.$store.state.midori.update;
    },
  },
  async created() {
    try {
      const res = await axios.get("/character");
      const enums = await axios.get("/game/enums")
      
      this.$store.commit("audio/setAudio")
      this.$store.commit("Roster/setData", res.data);
      this.$store.commit("enums/setEnums", enums.data);
      this.$store.commit("user/SET_USER", {
        _id: Math.random()
          .toString(36)
          .replace(/[^a-z]+/g, "")
          .substr(0, 10),
        username: `GUEST-${Math.random()
          .toString(36)
          .replace(/[^a-z]+/g, "")
          .substr(0, 2)}`,
        isLoggedIn: true,
        rank: "guest",
        avatar: Math.random()
          .toString(36)
          .replace(/[^a-z]+/g, "")
          .substr(0, 5),
      });
      this.loaded = true
      this.$store.dispatch("audio/playGameStart")
    } catch(err) {
      alert(err);
    }
  },
  mounted() {
    this.$store.dispatch("midori/setCanvas", this.$refs.canvas).then((r) => {
      this.imagesReady = r;
    });
  },
  // The Midori background has to be set by the root element so I watch for changes here
  watch: {
    update(n) {
      if (n) {
        if (this.$store.state.midori.index === 2) {
          this.$store.commit("midori/setHomeBackground");
        } else {
          this.$store.commit("midori/setTurnBackground");
        }
      }
      this.$store.state.midori.update = false;
    },
  },
  beforeDestroy() {
    this.$store.commit("midori/cleanUp");
  },
};
</script>

<style lang="scss">
@import "./style.scss";
</style>
