export default {
    namespaced:true,
    state: {
        pokemonTypes:{},
        damageTypes:{},
        activationTypes:{},
        effectTypes:{},
        costTypes:{},
        skillClassTypes:{},
        triggerClauseTypes:{},
        effectBehaviorTypes:{},
        targetModeTypes:{}
    },
    mutations:{
        setEnums: function(state, payload){
            state.effectBehaviorTypes = payload.effectTargetBehaviorTypings
            state.triggerClauseTypes = payload.triggerClauseTypings
            state.skillClassTypes = payload.skillClassTypings
            state.activationTypes = payload.activationTypings
            state.targetModeTypes = payload.targetModeTypings
            state.pokemonTypes = payload.pokemonTypings
            state.effectTypes = payload.effectTypings
            state.damageTypes = payload.damageTypings
            state.costTypes = payload.costTypings 
        }
    },
    getters:{
        pokemonTypes:function(state){
            return state.pokemonTypes
        },
        effectTypes:function(state){
            return state.effectTypes
        },
        activationTypes:function(state){
            return state.activationTypes
        },
        damageTypes:function(state){
            return state.damageTypes
        },
        costTypes:function(state){
            return state.costTypes
        },
        skillClassTypes:function(state){
            return state.skillClassTypes
        },
        targetModeTypes:function(state){
            return state.targetModeTypes
        },
        effectBehaviorTypes:function(state){
            return state.effectBehaviorTypes
        },
        triggerClauseTypes:function(state){
            return state.triggerClauseTypes
        } 
    }
}