export default {
  namespaced: true,
  state: {
    username: '',
    id: '',
    isLoggedIn: false,
    avatar: '',
    elo: 0,
    wins: 0,
    losses: 0,
    streak: 0
  },
  mutations: {
    SET_USER: function (state, payload) {
      state.id = payload._id;
      state.avatar = payload.avatar;
      state.username = payload.username;
      state.rank = payload.rank;
      state.elo = payload.elo || 0;
      state.isLoggedIn = true;
      state.wins = 0,
      state.losses = 0,
      state.streak = 0
    }
  },
  getters: {
    getUserData: (state) => {
      return { ...state }
    },
    ranked: (state) => {
      return { elo: state.elo, pid: state.id }
    },
  },
  actions: {
  },
}